.slide {
  display: flex;
  position: relative;
  width: 100%;
  height: 255px;
  margin: 2% 0;
  overflow: hidden;

  @media screen and (min-width: $breakpoint-desktop) {
    height: 415px;
  }
  //   &__image {
  //     width: 100%;
  //     height: 600px;
  //     border-radius: 10px;
  //   }
}
.slider {
  @include WH-full;
  position: absolute;
  left: 0%;
  right: 0%;
  top: 0%;
  bottom: 0%;
  border-radius: 10px;
  opacity: 0;
  transition: opacity ease-in-out 0.6s; //petit effet d'apparition progressive de l'image dans le slide

  @media screen and (min-width: $breakpoint-desktop) {
    border-radius: 15px;
  }
  & img {
    @include WH-full;
    object-fit: cover;
    position: absolute;
    left: 0%;
    right: 0%;
    top: 0%;
    bottom: 0%;
    mix-blend-mode: normal;
    border-radius: 10px;

    @media screen and (min-width: $breakpoint-desktop) {
      border-radius: 15px;
    }
  }
  &__number {
    display: none;

    @media screen and (min-width: $breakpoint-desktop) {
      display: block;
      position: absolute;
      @include font(18px, 500);
      color: $tertiary-color;
      left: 48%;
      top: 86%;
      // left: 48.95%;
      // right: 49.11%;
      // top: 86.19%;
      // bottom: 15.01%;
    }
  }
}
.active {
  opacity: 1;
}

.right-Arrow {
  @include rightArrow-style;
}

.left-Arrow {
  @include leftArrow-style;
}
